import React from 'react';

import {
    Box,
    Heading,
    Text,
    Flex,
  } from '@chakra-ui/react';

import ProjectItems from '../components/ProjectItems/ProjectItems';
import SkillProfile from '../components/SkillProfile/SkillProfile';

function IntroPage() {
  
    return (
      <>
        <Flex 
          flexWrap="wrap"
          justifyContent={'space-evenly'}
          width={'100%'}
        >
          <Flex 
            direction={'column'} 
            flexWrap={'wrap'}
            justifyContent={'flex-start'}
            minWidth={'330px'}
            width={'40%'}
          >
            <Box 
              bg='#152c59' 
              w='100%' 
              p={4} 
              minWidth={'330px'}
              margin={'10px 0px 10px 0px'}
              color='white' 
              padding='10px'
              borderRadius='10px'
            >
              <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
                About me
              </Heading>
              <Text 
                fontSize='md'>
                I am a software developer, currently working at Cognizant Technology Solutions. I have 3.5 years of experience in frontend web development with
                React JS and Redux. I am proficient in HTML, CSS, SCSS, Javascript. My interests also include backend development, databases, algorithms, data structures
                and physics.
              </Text>
            </Box>
            <Box 
              bg='#152c59' 
              w='100%' 
              p={4} 
              color='white' 
              margin={'10px 0px 10px 0px'}
              padding='10px'
              minWidth={'330px'}
              borderRadius='10px'
            >
              <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
                Projects
              </Heading>
              <ProjectItems 
                items={[{name: 'Algorithms and Data structures', linkTo: "/algorithmsAndDataStructures"}, 
                        {name: 'Data visualization', linkTo: "/dataVisualization"}, 
                        {name: 'Physics', linkTo: "/physics"},
                        {name: 'Chess', linkTo: "/chessBoard"}]} 
              />
            </Box>
          </Flex>
          <Box 
            bg='#152c59' 
            w='40%'
            margin={'10px 0px 10px 0px'}
            color='white' 
            padding='10px'
            minWidth={'330px'}
            borderRadius='10px'
          >
            <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
              My Skill Profile
            </Heading>
            <SkillProfile 
              items={[{name: 'React', score: 90}, {name: 'JavaScript', score: 80}, 
                      {name: 'HTML', score: 80}, {name: 'CSS, SCSS', score: 70}, 
                      {name: 'Python', score: 60}, {name: 'MySQL', score: 50},
                      {name: 'Node JS', score: 40}, {name: 'Java', score: 30}]} />
          </Box>
        </Flex>
      </>
    )
  }

export default IntroPage;
