import * as d3 from 'd3';

const margin = {top: 10, right: 10, bottom: 10, left: 10}

var data = {
    nodes: [{
      name: "A",
      x: 200,
      y: 150
    }, {
      name: "B",
      x: 140,
      y: 300
    }, {
      name: "C",
      x: 300,
      y: 300
    }, {
      name: "D",
      x: 300,
      y: 180
    }],
    links: [{
      source: 0,
      target: 1
    }, {
      source: 1,
      target: 2
    }, {
      source: 2,
      target: 3
    }, ]
  };

class graph {
  
  constructor(element) {
    let rect = element.getBoundingClientRect();
    console.log(rect);
    this.WIDTH = rect.width - margin.left - margin.right
    this.HEIGHT = 600 - margin.top - margin.bottom
    let nodeRadius = this.WIDTH/42.4;
    data = this.setNodePositions(data, nodeRadius)
    d3.select(element).select("svg").remove()
    this.draw(element, data, nodeRadius)
  }

  setNodePositions(data, radius) {
    let length = data.nodes.length;
    for(let i = 0; i < length; i++) {
      data.nodes[i].x = i*this.WIDTH/(1.2*length);
      data.nodes[i].y = this.HEIGHT/15;
    }
    return data;
  }
  
  draw(element, data, nodeRadius) {
  // append the svg object to the body of the page
  var radius = nodeRadius;
  var c10 = d3.scaleOrdinal(d3.schemeCategory10);
  var svg = d3.select(element)
  .append("svg")
    .attr("width", this.WIDTH)
    .attr("height", this.HEIGHT)
   .append("g")
    .attr("transform",
        "translate(" + 2*radius + "," + 2*radius + ")");

    var links = svg.selectAll("link")
    .data(data.links)
    .enter()
    .append("line")
    .attr("class", "link")
    .attr("marker-end", "url(#end)")
    .attr("x1", function(l) {
        var sourceNode = data.nodes.filter(function(d, i) {
        return i === l.source
        })[0];
        d3.select(this).attr("y1", sourceNode.y);
        return sourceNode.x
    })
    .attr("x2", function(l) {
        var targetNode = data.nodes.filter(function(d, i) {
        return i === l.target
        })[0];
        d3.select(this).attr("y2", targetNode.y);
        return targetNode.x
    })
    .attr("fill", "#000")
    .attr("stroke", "#000");

    var topArrows = svg.selectAll('arrow')
    .data(data.nodes)
        .enter() 
        .append("line")
        .attr("class", "arrow")
        .attr("x1", function(l) {
        let x;
        if (!l.last) {
            x = l.x + 2.5*radius;
        }
        return x;
        })
        .attr("y1", function(l) {
        let y;
        if (!l.last) {
            return l.y;
        }
        return y;
        })
        .attr("x2", function(l) {
        let x;
        if (!l.last) {
            x = l.x + 2.5*radius-radius/2;
        }
        return x;
        })
        .attr("y2", function(l) {
        let y;
        if (!l.last) {
            y = l.y+5
        }
        return y; 
        })
        .attr("fill", "#000")
        .attr("stroke", "#000");
        
    var bottomArrows = svg.selectAll('arrow')
    .data(data.nodes)
        .enter() 
        .append("line")
        .attr("class", "arrow")
        .attr("x1", function(l) {
        let x;
        if (!l.last) {
            x = l.x + 2.5*radius;
        }
        return x;
        })
        .attr("y1", function(l) {
        let y;
        if (!l.last) {
            return l.y;
        }
        return y;
        })
        .attr("x2", function(l) {
        let x;
        if (!l.last) {
            x = l.x +2.5*radius-radius/2;
        }
        return x;
        })
        .attr("y2", function(l) {
        let y;
        if (!l.last) {
            y = l.y-5
        }
        return y; 
        })
        .attr("fill", "#000")
        .attr("stroke", "#000");
        
    var nodes = svg.selectAll("node")
    .data(data.nodes)
    .enter()
    .append("circle")
    .attr("class", "node")
    .attr("cx", function(d) {
        return d.x
    })
    .attr("cy", function(d) {
        return d.y
    })
    .attr("r", radius)
    .attr("fill", function(d, i) {
        return c10(i);
    })
    let nodeNames = svg.selectAll("node")
        .data(data.nodes)
        .enter()
        .append('text')
        .attr("x", function(d){return d.x - radius/3})
        .attr("y", function(d){return d.y + radius/5})
        .style("font-size", `${radius}px`)
        .style("font-weight", 'bold')
        .text(function(d){return d.name})
    }
}

export default graph